import React, { useMemo } from 'react'

interface SvgIconProps {
  /**
   * 
   * @default icon
   */
  prefix?: string
  /**
   *   dir-name 
   */
  name: string
  /**
   * fill
   */
  color?: string
  /**
   * stroke
   */
  stroke?: string
  /**
   * 
   * @default 16
   */
  size?: number | string
  /**
   * 
   */
  onClick?: () => void
  /**
   * css 
   */
  className?: string
  /**
   * css 
   */
  style?: React.CSSProperties
}

export const SvgIcon = (props: SvgIconProps) => {
  const { prefix = 'icon', name, color = 'currentColor', size = 16, stroke, ...attrs } = props

  const symbolId = useMemo(() => `#${prefix}-${name}`, [prefix, name])

  return (
    <svg aria-hidden="true" width={size} height={size} {...attrs} style={{ verticalAlign: '-0.3em' }}>
      <use href={symbolId} fill={color} stroke={stroke} />
    </svg>
  )
}
